import React from 'react'
import Carousel from 'react-grid-carousel'
const blogs = [
    {
        url: 'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/blog/blog1.png',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail: 'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/blog/blog2.png',
    },
    {
        url: 'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/blog/blog3.png',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail: 'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/blog/blog4.png',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail: 'Up To 40% Off Final Sale Items.'
    },




];
const Blog = () => {
    return (
        <>
            <section class="blog__section section--padding pt-0">
                <div class="container-fluid">
                    <div class="section__heading text-center mb-40">
                        <h2 class="section__heading--maintitle">From The Blog</h2>
                    </div>
                    <Carousel cols={4} rows={1} gap={20} loop>

                        {blogs.map(blog => (
                                <Carousel.Item key={blog.id}>
                                <div class="blog__section--inner blog__swiper--activation swiper">
                                    <div class="swiper-wrapper">
                                        <div class="swiper-slide">
                                            <div class="blog__items">
                                                <div class="blog__thumbnail">
                                                    <a class="blog__thumbnail--link">
                                                        <img class="blog__thumbnail--img" src={blog.url} alt="blog-img" /></a>
                                                </div>
                                                <div class="blog__content">
                                                    <span class="blog__content--meta">February 03, 2022</span>
                                                    <h3 class="blog__content--title"><a >Fashion Trends In 2021 Styles,
                                                        Colors, Accessories</a></h3>
                                                    <a class="blog__content--btn primary__btn" >Read more </a>
                                                </div>
                                            </div>
                                        </div>
                                        
                                        
                                    </div>
                                </div>
                                </Carousel.Item>
                        ))}
                    </Carousel>
            </div>
        </section >
        
</>

    )
}

export default Blog