import{combineReducers,applyMiddleware} from "redux";
import {legacy_createStore as createStore} from 'redux'

import thunk from "redux-thunk";
import {composeWithDevTools } from "redux-devtools-extension";
import {GetProductDetailReducer, ProductReducer, newReviewReducer} from  "./Redux/Reducers/ProductReducers"
import { forgotPasswordReducer, loginReducer, profileReducer } from "./Redux/Reducers/UserReducers";
import { cartReducer } from "./Redux/Reducers/CartReducer";
import {
  allOrdersReducer,
  myOrdersReducer,
  newOrderReducer,
  orderDetailsReducer,
  orderDetailsReducer1,
  orderReducer,
} from "./Redux/Reducers/OrderReducers";
const Reducer =combineReducers({
    products:ProductReducer,
    productDetail:GetProductDetailReducer,
    user:loginReducer,
    userprofile:profileReducer,
    forgotPassword:forgotPasswordReducer,
    cart:cartReducer,
    newOrder: newOrderReducer,
  myOrders: myOrdersReducer,
  orderDetails: orderDetailsReducer,
  newReview: newReviewReducer,

});
const middleware =[thunk]
let initialState = {
  cart: {
    cartItems: localStorage.getItem("cartItems")
      ? JSON.parse(localStorage.getItem("cartItems"))
      : [],
    shippingInfo: localStorage.getItem("shippingInfo")
      ? JSON.parse(localStorage.getItem("shippingInfo"))
      : {},
  },
};
const store = createStore(
    Reducer,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
  );

export default store;
