import logo from './logo.svg';
import './App.css';
import Person from './Person/person.js';
import React, { useState, useEffect } from 'react';
import UserInput from './Assigment/UserInput';
import UserOutput from './Assigment/UserOutput';
import Header from './Header/Header';
import Aboutus from './AboutUs/aboutus';
import Main from './Main/Main';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import { useSelector } from "react-redux";

import ProductDetail from './Main/ProductDetail';
import Product from './Main/Product';
import Cart from './Main/Cart';
import Shop from './Shop/Shop';
import Contactus from './Contact-us/Contactus';
import Login from "./Accounts/login"
import Profile from './Accounts/profile';
import store from "./Store";
import { loadUser } from './Redux/Actions/UserAction';
import Forgotpassword from './Accounts/Forgotpassword';
import Resetpassword from './Accounts/Resetpassword';
import Shipping from './Shipping/Shipping';
import ProtectedRoute from './ProtectedRoute'
import ConfrimOrder from './Order/ConfrimOrder';
import Payment from './Order/Payment';
import axios from 'axios';
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";
import Success from './Order/Success';
import OrderDetails from './Order/OrderDetails';

function App() {
  const { isAnthentication, user } = useSelector((state) => state.user);
  const [stripeApiKey, setStripeApiKey] = useState("");

  async function getStripeApiKey() {
    const { data } = await axios.get("/api/v1/stripeapikey");

    setStripeApiKey(data.stripeApiKey);
  }
  useEffect(() => {


    store.dispatch(loadUser());

    getStripeApiKey();
  }, []);
  return (
    <>







      <BrowserRouter>

        <Routes>
      {stripeApiKey && (
  <Route
    path="/process/payment"
    element={(
      <Elements stripe={loadStripe(stripeApiKey)}>
        <Payment />
      </Elements>
    )}
  />
)}

          <Route path="/" element={<Main />} />
          <Route path="/aboutus" element={<Aboutus />} />
          <Route exact path="/products" element={<Shop />} />
          <Route exact path="/contactus" element={<Contactus />} />

          <Route exact path="/products/:keyword" element={<Shop />} />

          <Route path="/cart" element={<Cart />} />
          <Route path="/login" element={<Login />} />
         

          <Route path="/login/shipping" element={<Shipping/>} />
          <Route path="/order/confirm" element={<ConfrimOrder/>} />




          <Route path="/forgotpassword" element={<Forgotpassword />} />
          <Route path="/password/reset/:token" element={<Resetpassword />} />
          <Route path="/success" element={isAnthentication && <Success user={user} />} />
          <Route path="/account" element={isAnthentication && <Profile user={user} />} />


          <Route path="/Product-detail/:id" element={<ProductDetail />} />
          <Route path="/order/:id" element={<OrderDetails/>} />
        </Routes>

      </BrowserRouter>


    </>


  );
}

export default App;
