import React, { useEffect, useState } from 'react'
import { changepassword, clearErrors, loadUser, logout, updateprofile } from '../Redux/Actions/UserAction';
import { useDispatch, useSelector } from 'react-redux'
import { useAlert } from 'react-alert'
import { Link, useNavigate } from 'react-router-dom';
import Header from '../Header/Header';
import "./Profile.css"
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import { UPDATE_PROFILE_RESET } from '../Redux/Constants/UserConstants';
import { myOrders } from '../Redux/Actions/OrderAction';
import LaunchIcon from "@material-ui/icons/Launch";
import Footer from '../Footer/Footer';


const Profile = ({ history, user }) => {


    const dispatch = useDispatch();
    const alert = useAlert();
    let navigate = useNavigate();
    const { isUpdated, error } = useSelector((state) => state.userprofile);
    const { loading, orders } = useSelector((state) => state.myOrders);

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [avatar, setAvatar] = useState();
    const [avatarPreview, setAvatarPreview] = useState("/Profile.png");
    const [oldPassword, setOldPassword] = useState("");
    const [newPassword, setNewPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const updateProfileSubmit = (e) => {
        e.preventDefault();

        const myForm = new FormData();

        myForm.set("name", name);
        myForm.set("email", email);
        myForm.set("avatar", avatar);
        dispatch(updateprofile(myForm));
    };
    const updatePasswordSubmit = (e) => {
        e.preventDefault();

        const myForm = new FormData();

        myForm.set("oldPassword", oldPassword);
        myForm.set("newPassword", newPassword);
        myForm.set("confirmPassword", confirmPassword);

        dispatch(changepassword(myForm));
    };
    const updateProfileDataChange = (e) => {
        const reader = new FileReader();

        reader.onload = () => {
            if (reader.readyState === 2) {
                setAvatarPreview(reader.result);
                setAvatar(reader.result);
            }
        };

        reader.readAsDataURL(e.target.files[0]);
    };


    function logoutUser() {
        dispatch(logout());
        alert.success("Logout Successfully");
        navigate("/");

    }
    const [value, setValue] = React.useState('one');

    const handleChange = (event: React.SyntheticEvent, newValue) => {
        setValue(newValue);
    };
    useEffect(() => {
        if (user) {
            setName(user.name);
            setEmail(user.email);
            setAvatarPreview(user.avatar.url);
        }

        if (error) {
            alert.error(error);
            dispatch(clearErrors());
        }

        if (isUpdated) {
            alert.success("Profile Updated Successfully");
            dispatch(loadUser());

            navigate("/account");

            dispatch({
                type: UPDATE_PROFILE_RESET,
            });
        }
        dispatch(myOrders());

    }, [dispatch, error, alert, history, user, isUpdated]);
    return (
        <>
            <Header />
       
            <main class="main__content_wrapper">

                {/* <!-- Start breadcrumb section --> */}
                <section class="breadcrumb__section breadcrumb__bg">
                    <div class="container">
                        <div class="row row-cols-1">
                            <div class="col">
                                <div class="breadcrumb__content text-center">
                                    <h1 class="breadcrumb__content--title text-white mb-25">My Account</h1>
                                    <ul class="breadcrumb__content--menu d-flex justify-content-center">
                                        <li class="breadcrumb__content--menu__items"><a class="text-white" ><Link to="/">Home</Link></a></li>
                                        <li class="breadcrumb__content--menu__items"><span class="text-white">My Account</span></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <Box className="my__account--section__inner border-radius-10 ">
                    <TabContext value={value}>
                        <Box>
                            <TabList class="container-xl px-4 mt-4" onChange={handleChange} aria-label="">

                                <Tab style={{ color: "#69707a", fontSize: "15px" }} label="Profile" value="one" />
                                <Tab style={{ color: "#69707a", fontSize: "15px" }} label="Edit Profile" value="four" />

                                <Tab style={{ color: "#69707a", fontSize: "15px" }} label="My Orders" value="two" />
                                <Tab style={{ color: "#69707a", fontSize: "15px" }} label="Change Password" value="three" />
                                <Tab style={{ color: "#69707a", fontSize: "15px" }} label="Logout" onClick={logoutUser} />


                            </TabList>

                        </Box>
                        <TabPanel value="one">
                            <div className="items-Description selected">
                                <div class="">
                                    <div class="container-xl px-4 mt-4">

                                        <div class="row">
                                            <div class="col-xl-4">
                                                {/* <!-- Profile picture card--> */}
                                                <div class="card mb-4 mb-xl-0">
                                                    <div class="card-header">Profile Picture</div>
                                                    <div class="card-body text-center">
                                                        {/* <!-- Profile picture image--> */}
                                                        <img src={avatarPreview} alt="Avatar Preview" />

                                                    </div>
                                                </div>
                                            </div>
                                            <div class="col-xl-8">
                                                {/* <!-- Account details card--> */}
                                                <div class="card mb-4">
                                                    <div class="card-header">Account Details</div>
                                                    <div class="card-body">
                                                        <form>
                                                            {/* <!-- Form Group (username)-->/ */}
                                                            <div class="mb-3">
                                                                <label class="small mb-1" for="inputUsername" style={{ color: "#69707a", fontSize: "15px" }}>Username (how your name will appear to other users on the site)</label>
                                                                <div class="form-control" id="inputUsername"> {user.name}</div>
                                                            </div>
                                                            {/* <!-- Form Row--> */}
                                                            <div class="mb-3">
                                                                <label class="small mb-1" for="inputEmailAddress" style={{ color: "#69707a", fontSize: "15px" }}>Email address</label>
                                                                <div class="form-control" id="inputUsername"> {user.email}</div>
                                                            </div>

                                                            {/* <!-- Form Group (email address)--> */}
                                                            <div class="mb-3">
                                                                <label class="small mb-1" for="inputEmailAddress" style={{ color: "#69707a", fontSize: "15px" }}>Joined On</label>
                                                                <div class="form-control" id="inputUsername"> {String(user.createdAt).substr(0, 10)}</div>
                                                            </div>
                                                            {/* <!-- Form Row--> */}

                                                            {/* <!-- Save changes button--> */}
                                                        </form>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value="four">
                            <div class="container-xl px-4 mt-4">

                                <div class="row">
                                    <div class="col-xl-4">
                                        {/* <!-- Profile picture card--> */}
                                        <div class="card mb-4 mb-xl-0">
                                            <div class="card-header">Profile Picture</div>
                                            <div class="card-body text-center">
                                                {/* <!-- Profile picture image--> */}
                                                <img src={avatarPreview} alt="Avatar Preview" />
                                                <input
                                                    type="file"
                                                    required
                                                    name="avatar"
                                                    accept="image/*"
                                                    onChange={updateProfileDataChange}
                                                />
                                                <button class="account__login--btn primary__btn" type="submit"
                                                    value="Update">update</button>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-xl-8">
                                        {/* <!-- Account details card--> */}
                                        <div class="card mb-4">
                                            <div class="card-header">Account Details</div>
                                            <div class="card-body">
                                                <form encType="multipart/form-data"
                                                    onSubmit={updateProfileSubmit}>
                                                    {/* <!-- Form Group (username)--> */}
                                                    <div class="mb-3">
                                                        <label class="small mb-1" for="inputUsername">Username (how your name will appear to other users on the site)</label>
                                                        <input class="form-control" id="inputUsername"
                                                            type="text" placeholder="Enter your username"
                                                            required
                                                            name="name"
                                                            value={name}
                                                            onChange={(e) => setName(e.target.value)}
                                                        />
                                                    </div>
                                                    {/* <!-- Form Row--> */}
                                                    <div class="row gx-3 mb-3">
                                                        {/* <!-- Form Group (first name)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputFirstName">First name</label>
                                                            <input class="form-control" id="inputFirstName" type="text"
                                                                placeholder="Enter your first name" required
                                                                name="email"
                                                                value={email}
                                                                onChange={(e) => setEmail(e.target.value)} />
                                                        </div>
                                                        {/* <!-- Form Group (last name)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputLastName">Last name</label>
                                                            <input class="form-control" id="inputLastName" type="text" placeholder="Enter your last name" value="Luna" />
                                                        </div>
                                                    </div>
                                                    {/* <!-- Form Row        --> */}
                                                    <div class="row gx-3 mb-3">
                                                        {/* <!-- Form Group (organization name)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputOrgName">Organization name</label>
                                                            <input class="form-control" id="inputOrgName" type="text" placeholder="Enter your organization name" value="Start Bootstrap" />
                                                        </div>
                                                        {/* <!-- Form Group (location)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputLocation">Location</label>
                                                            <input class="form-control" id="inputLocation" type="text" placeholder="Enter your location" value="San Francisco, CA" />
                                                        </div>
                                                    </div>
                                                    {/* <!-- Form Group (email address)--> */}
                                                    <div class="mb-3">
                                                        <label class="small mb-1" for="inputEmailAddress">Email address</label>
                                                        <input class="form-control" id="inputFirstName" type="text"
                                                            placeholder="Enter your first name" required
                                                            name="email"
                                                            value={email}
                                                            onChange={(e) => setEmail(e.target.value)} />                                                    </div>
                                                    {/* <!-- Form Row--> */}
                                                    <div class="row gx-3 mb-3">
                                                        {/* <!-- Form Group (phone number)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputPhone">Phone number</label>
                                                            <input class="form-control" id="inputPhone" type="tel" placeholder="Enter your phone number" value="555-123-4567" />
                                                        </div>
                                                        {/* <!-- Form Group (birthday)--> */}
                                                        <div class="col-md-6">
                                                            <label class="small mb-1" for="inputBirthday">Birthday</label>
                                                            <input class="form-control" id="inputBirthday" type="text" name="birthday" placeholder="Enter your birthday" value="06/10/1988" />
                                                        </div>
                                                    </div>
                                                    {/* <!-- Save changes button--> */}
                                                    <button class="account__login--btn primary__btn" type="submit"
                                                        value="Update">update</button>
                                                </form>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </TabPanel>
                        <TabPanel value="two">
                            <div className="items-Tags">
                                <div class="container-xl px-4 mt-4">



                                    {/* <!-- Billing history card--> */}
                                    <div class="card mb-4">
                                        <div class="card-header">Billing History</div>
                                        <div class="card-body p-0">
                                            {/* <!-- Billing history table--> */}
                                            <div class="table-responsive table-billing-history">
                                                <table class="table mb-0">
                                                    <thead>
                                                        <tr>
                                                            <th class="border-gray-200" scope="col">Transaction ID</th>
                                                            <th class="border-gray-200" scope="col">Date</th>
                                                            <th class="border-gray-200" scope="col">Amount</th>
                                                            <th class="border-gray-200" scope="col">Status</th>
                                                            <th class="border-gray-200" scope="col">Actions</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {orders && orders.map((item, index) => (
                                                            <>
                                                                <tr>
                                                                    <td>{item._id}</td>
                                                                    <td>{String(item.createdAt).substr(0, 10)}</td>
                                                                    <td>{item.totalPrice}</td>
                                                                    <td><span class="badge bg-light text-dark">{item.orderStatus}</span></td>
                                                                    <td><span class="badge bg-light text-dark"><Link to={`/order/${item._id}`}>
                                                                        <LaunchIcon />
                                                                    </Link></span></td>
                                                                </tr>

                                                            </>
                                                        ))}
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>                    </div>

                        </TabPanel>
                        <TabPanel value="three">
                            <div class="container-xl px-4 mt-4">
                                {/* <!-- Account page navigation--> */}

                                {/* <hr class="mt-0 mb-4"> */}
                                <div class="row">
                                    <div class="col-lg-8">
                                        {/* <!-- Change password card--> */}
                                        <div class="card mb-4">
                                            <div class="card-header">Change Password</div>
                                            <div class="card-body">
                                                <form onSubmit={updatePasswordSubmit}>
                                                    {/* <!-- Form Group (current password)--> */}
                                                    <div class="mb-3">
                                                        <label class="small mb-1" for="currentPassword">Current Password</label>
                                                        <input class="form-control" id="currentPassword" type="password"
                                                            placeholder="Old Password"
                                                            required
                                                            value={oldPassword}
                                                            onChange={(e) => setOldPassword(e.target.value)} />
                                                    </div>
                                                    {/* <!-- Form Group (new password)--> */}
                                                    <div class="mb-3">
                                                        <label class="small mb-1" for="newPassword">New Password</label>
                                                        <input class="form-control" id="newPassword"
                                                            placeholder="Enter new password"
                                                            required
                                                            value={newPassword}
                                                            onChange={(e) => setNewPassword(e.target.value)}
                                                        />
                                                    </div>
                                                    {/* <!-- Form Group (confirm password)--> */}
                                                    <div class="mb-3">
                                                        <label class="small mb-1" for="confirmPassword">Confirm Password</label>
                                                        <input class="form-control" id="confirmPassword" type="password"
                                                            placeholder="Confirm Password"
                                                            required
                                                            value={confirmPassword}
                                                            onChange={(e) => setConfirmPassword(e.target.value)} />
                                                    </div>
                                                    <button class="account__login--btn primary__btn" type="submit"
                                                        value="Change">change Password</button>                                                </form>
                                            </div>
                                        </div>
                                        {/* <!-- Security preferences card--> */}

                                    </div>
                                    <div class="col-lg-4">
                                        {/* <!-- Two factor authentication card--> */}
                                        <div class="card mb-4">
                                            <div class="card-header">Two-Factor Authentication</div>
                                            <div class="card-body">
                                                <p>Add another level of security to your account by enabling two-factor authentication. We will send you a text message to verify your login attempts on unrecognized devices and browsers.</p>
                                                <form>
                                                    <div class="form-check">
                                                        <input class="form-check-input" id="twoFactorOn" type="radio" name="twoFactor" checked="" />
                                                        <label class="form-check-label" for="twoFactorOn">On</label>
                                                    </div>
                                                    <div class="form-check">
                                                        <input class="form-check-input" id="twoFactorOff" type="radio" name="twoFactor" />
                                                        <label class="form-check-label" for="twoFactorOff">Off</label>
                                                    </div>
                                                    <div class="mt-3">
                                                        <label class="small mb-1" for="twoFactorSMS">SMS Number</label>
                                                        <input class="form-control" id="twoFactorSMS" type="tel" placeholder="Enter a phone number" value="555-123-4567" />
                                                    </div>
                                                </form>
                                            </div>
                                        </div>
                                        {/* <!-- Delete account card--> */}

                                    </div>
                                </div>
                            </div>





                        </TabPanel >
                    </TabContext>
                </Box>
                        {/* <!-- Start shipping section --> */}
        <section class="shipping__section2 shipping__style3 section--padding">
            <div class="container">
                <div class="shipping__section2--inner shipping__style3--inner d-flex justify-content-between">
                    <div class="shipping__items2 d-flex align-items-center">
                        <div class="shipping__items2--icon">
                            <img src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/other/shipping1.png'} alt=""/>
                        </div>
                        <div class="shipping__items2--content">
                            <h2 class="shipping__items2--content__title h3">Shipping</h2>
                            <p class="shipping__items2--content__desc">From handpicked sellers</p>
                        </div>
                    </div>
                    <div class="shipping__items2 d-flex align-items-center">
                        <div class="shipping__items2--icon">
                            <img src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/other/shipping2.png'} alt=""/>
                        </div>
                        <div class="shipping__items2--content">
                            <h2 class="shipping__items2--content__title h3">Payment</h2>
                            <p class="shipping__items2--content__desc">From handpicked sellers</p>
                        </div>
                    </div>
                    <div class="shipping__items2 d-flex align-items-center">
                        <div class="shipping__items2--icon">
                            <img src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/other/shipping3.png'} alt=""/>
                        </div>
                        <div class="shipping__items2--content">
                            <h2 class="shipping__items2--content__title h3">Return</h2>
                            <p class="shipping__items2--content__desc">From handpicked sellers</p>
                        </div>
                    </div>
                    <div class="shipping__items2 d-flex align-items-center">
                        <div class="shipping__items2--icon">
                            <img src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/other/shipping4.png'} alt=""/>
                        </div>
                        <div class="shipping__items2--content">
                            <h2 class="shipping__items2--content__title h3">Support</h2>
                            <p class="shipping__items2--content__desc">From handpicked sellers</p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        {/* <!-- End shipping section --> */}
        <Footer/>
            </main>


        </>


    )
}

export default Profile