import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Header from '../Header/Header';
import Banner from './Banner'
import BestSeller from './BestSeller';
import Deals from './Deals';
import './Main.css'
import './Banner.css'
import Product from './Product'
import ProductDetail from './ProductDetail';
import Slider from './Slider'
import Client from './Client';
import { commerce } from '../lib/Commerce';
import Blog from './Blog';
import Footer from '../Footer/Footer';
import ShopSection from './ShopSection';
import { useDispatch, useSelector } from 'react-redux';
import { getAllProduct } from '../Redux/Actions/Productaction';
import { useAlert } from 'react-alert';
import { Link, useParams } from 'react-router-dom';
const Main = () => {
    const alert = useAlert();

    const options = {
        // value: product.ratings,
        readOnly: true,
        precision: 0.5,
    };
    const dispatch = useDispatch();

    const { loading, error, products } = useSelector((state) => state.products);
    const { numOfReviews } = useSelector((state) => state.products);
    const { keyword } = useParams();

    useEffect(() => {
        if (error) {
            return alert.error(error)
        }

        dispatch(getAllProduct(keyword))
    }, [dispatch, keyword])


    const [smShow, setSmShow] = useState(false);
    const [lgShow, setLgShow] = useState(false);

    return (
        <>

            <main class="main__content_wrapper">
                <Header />
                {/* <!-- Start slider section --> */}
                <Slider />
                {/* <!-- End slider section --> */}

                {/* <!-- Start banner section --> */}
                <Banner />
                {/* <!-- End banner section --> */}

                {/* <!-- Start product section --> */}
                <section class="product__section section--padding pt-0">
                    <div class="container-fluid">
                        <div class="section__heading text-center mb-35">
                            <h2 class="section__heading--maintitle">New Products</h2>
                        </div>
                      
                        <div class="tab_content">
                            <div id="featured" class="tab_pane active show">
                                <div class="product__section--inner">
                                    <div class="row row-cols-xl-5 row-cols-lg-4 row-cols-md-3 row-cols-2 mb--n30">

                                        

                                            {products &&
                                                products.map((product) => (
                                                    <ShopSection key={product._id} product={product} />
                                                ))}
                                        </div>


                                </div>
                            </div>
                        </div>
                    </div>

                </section>



                {/* <!-- End product section --> */}

                {/* <!-- Start deals banner section --> */}
                <Deals />
                {/* <!-- End deals banner section --> */}

                {/* <!-- Start product section --> */}
                <BestSeller />
                {/* <!-- End product section --> */}

                {/* <!-- Start banner section --> */}
                <section class="banner__section section--padding pt-0">
                    <div class="container-fluid">
                        <div class="row row-cols-md-2 row-cols-1 mb--n28">
                            <div class="col mb-28">
                                <div class="banner__items position__relative">
                                    <a class="banner__items--thumbnail "><Link to="/products"><img class="banner__items--thumbnail__img banner__img--max__height" src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/banner/banner5.png'} alt="banner-img" /></Link>
                                        <div class="banner__items--content">
                                            <span class="banner__items--content__subtitle d-none d-lg-block">Pick Your Items</span>
                                            <h2 class="banner__items--content__title h3">Up to 25% Off Order Now</h2>
                                            <span class="banner__items--content__link"><u>Shop now</u></span>
                                        </div>
                                    </a>
                                </div>
                            </div>
                            <div class="col mb-28">
                                <div class="banner__items position__relative">
                                    <a class="banner__items--thumbnail " ><a to="/products"><img class="banner__items--thumbnail__img banner__img--max__height" src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/banner/banner6.png'} alt="banner-img" /></a>
                                        <div class="banner__items--content">
                                            <span class="banner__items--content__subtitle d-none d-lg-block">Special offer</span>
                                            <h2 class="banner__items--content__title h3">Up to 35% Off Order Now</h2>
                                            <span class="banner__items--content__link"><u>Discover Now</u> </span>
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* <!-- End banner section --> */}

                {/* <!-- Start testimonial section --> */}
                <Client />
                {/* <!-- End testimonial section --> */}

                {/* <!-- Start banner section --> */}
                <section class="banner__section section--padding pt-0">
                    <div class="container-fluid">
                        <div class="row row-cols-1">
                            <div class="col">
                                <div class="banner__section--inner position__relative">
                                    <a class="banner__items--thumbnail display-block" ><Link to="/products">
                                        <img class="banner__items--thumbnail__img banner__img--height__md display-block"
                                            src={'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/banner/banner-bg2.png'} alt="banner-img" /></Link>
                                        <div class="banner__content--style2">
                                            <h2 class="banner__content--style2__title text-white">Need Winter Boots? </h2>
                                            <p class="banner__content--style2__desc">Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua enim ad minim veniam, quis nostrud exercitation </p>
                                            <span class="primary__btn">Shop Now
                                                <svg class="primary__btn--arrow__icon" xmlns="http://www.w3.org/2000/svg" width="20.2" height="12.2" viewBox="0 0 6.2 6.2">
                                                    <path d="M7.1,4l-.546.546L8.716,6.713H4v.775H8.716L6.554,9.654,7.1,10.2,9.233,8.067,10.2,7.1Z" transform="translate(-4 -4)" fill="currentColor"></path>
                                                </svg>
                                            </span>
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                {/* <!-- End banner section --> */}

                {/* <!-- Start blog section --> */}
                <Blog />
                {/* <!-- End blog section --> */}
                {/* <!-- Start footer section --> */}
                <Footer />
                {/* <!-- End footer section --> */}

            </main>

        </>

    )
}

export default Main