import React from 'react'
import './Slider.css'
import { Zoom } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css'
import { Link } from 'react-router-dom';
const images = [
    {
        url: 'https://cdn.shopify.com/s/files/1/0624/8638/0738/files/a-slider3.png?crop=center&height=668&v=1680601290&width=1903',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail:'Up To 40% Off Final Sale Items.'
    },
    {
        url: 'https://cdn.shopify.com/s/files/1/0036/7306/3491/files/dome1-bnr1_1600x.jpg?v=1614360791',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail:'Up To 40% Off Final Sale Items.'
    },
   
    {
        url: 'https://cdn.shopify.com/s/files/1/0624/8638/0738/files/a-slider1.png?crop=center&height=668&v=1680601088&width=1903',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail:'Up To 40% Off Final Sale Items.'   
     },
    {
        url: 'https://risingtheme.com/html/demo-suruchi-v1/suruchi/assets/img/slider/home1-slider1.png',
        caption: 'The Great Fashion ',
        subcaption: 'Collection 2022',
        detail:'Up To 40% Off Final Sale Items.'   
     },
    

];
const Slider = () => {

    return (
        <>


            <Zoom scale={0.4}>

                {
                    images.map((image, index) =>
                        <div key={index}>
                            <section class="hero__slider--section">

                                <div class="hero__slider--inner hero__slider--activation swiper">
                                    <div class="hero__slider--wrapper swiper-wrapper">

                                        <div class="slide-container ">

                                            <div class="hero__slider--items ">

                                                <div class="container-fulid">
                                                    <img style={{ width: "100%" }} src={image.url} />

                                                    <div class="">
                                                        <div class="row row-cols-1">
                                                            <div class="col">
                                                                <div class="slider__content">

                                                                    <h2 class="slider__content--maintitle h1"><h2>{image.caption}</h2>
                                                                        <br></br>
                                                                        {image.subcaption}</h2>
                                                                    <p class="slider__content--desc desc2 d-sm-2-none mb-40">{image.detail} <br></br>
                                                                        Caught in the Moment!</p>
                                                                    <a class="slider__btn primary__btn" ><Link to="/products">Show Collection
                                                                        <svg class="primary__btn--arrow__icon" xmlns="http://www.w3.org/2000/svg" width="20.2" height="12.2" viewBox="0 0 6.2 6.2">
                                                                            <path d="M7.1,4l-.546.546L8.716,6.713H4v.775H8.716L6.554,9.654,7.1,10.2,9.233,8.067,10.2,7.1Z" transform="translate(-4 -4)" fill="currentColor"></path>
                                                                        </svg>
                                                                        </Link>
                                                                    </a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </div>

                    )
                }
            </Zoom>













        </>)
}

export default Slider