import { useState, useEffect } from 'react';
import './Deals.css'
import { Link } from 'react-router-dom'
const Deals = ({ delayResend = "180" }) => {
    const [delay, setDelay] = useState(+delayResend);
  const houre = Math.floor(delay / 120);
  const minutes = Math.floor(delay / 60);

  const seconds = Math.floor(delay % 60);
  useEffect(() => {
    const timer = setInterval(() => {
      setDelay(delay - 1);
    }, 1000);

    if (delay === 0) {
      clearInterval(timer);
    }

    return () => {
      clearInterval(timer);
    };
  });



  return (
<>

<section class="deals__banner--section section--padding pt-0">
            <div class="container-fluid">
                <div class="deals__banner--inner banner__bg">
                    <div class="row row-cols-1 align-items-center">
                        <div class="col">
                            <div class="deals__banner--content position__relative">
                                <span class="deals__banner--content__subtitle text__secondary">Hurry up and Get 25% Discount</span>
                                <h2 class="deals__banner--content__maintitle">Deals Of The Day</h2>
                                <p class="deals__banner--content__desc"> New Offer Products </p>
                                
                                <div class="deals__banner--countdown d-flex" data-countdown="Sep 30, 2022 00:00:00"><div class="countdown__item" >
                                    <span class="countdown__number">7</span>
                                    <p class="countdown__text">days</p></div>
                                    <div class="countdown__item" ><span class="countdown__number">{houre}</span>
                                    <p class="countdown__text">hrs</p></div><div class="countdown__item" >
                                        <span class="countdown__number">{minutes}</span><p class="countdown__text">mins</p></div>
                                        <div class="countdown__item" ><span class="countdown__number">{seconds}</span><p class="countdown__text">secs</p></div></div>
                                <a class="primary__btn" ><Link to="/products">Show Collection
                                    <svg class="primary__btn--arrow__icon" xmlns="http://www.w3.org/2000/svg" width="20.2" height="12.2" viewBox="0 0 6.2 6.2">
                                    <path d="M7.1,4l-.546.546L8.716,6.713H4v.775H8.716L6.554,9.654,7.1,10.2,9.233,8.067,10.2,7.1Z" transform="translate(-4 -4)" fill="currentColor"></path>
                                    </svg></Link>
                                </a>
                                <br></br>
                              
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
</>

    )
}

export default Deals