import React, { Fragment, useEffect, useState } from "react";
// import "./orderDetails.css";
import { useSelector, useDispatch } from "react-redux";
// import MetaData from "../layout/MetaData";
import { Link, useParams } from "react-router-dom";
import { Typography } from "@material-ui/core";
import { getOrderDetails, clearErrors } from "../Redux/Actions/OrderAction";
// import Loader from "../layout/Loader/Loader";
import { useAlert } from "react-alert";
import Header from "../Header/Header";

const OrderDetails = ({ match }) => {
  window.scrollTo(0, 0);
  const [sdkReady, setSdkReady] = useState(false);
  const { id } = useParams();
  const dispatch = useDispatch();

  const orderDetails = useSelector((state) => state.orderDetails);
  const { order, orderItems, shippingInfo, loading, paymentInfo, error, user } = orderDetails;
  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }

    dispatch(getOrderDetails(id));

  }, [dispatch, id]);






  return (
    <>
      <Header/>
      <main class="main__content_wrapper">

{/* <!-- Start breadcrumb section --> */}
<section class="breadcrumb__section breadcrumb__bg">
    <div class="container">
        <div class="row row-cols-1">
            <div class="col">
                <div class="breadcrumb__content text-center">
                    <h1 class="breadcrumb__content--title text-white mb-25">My Account</h1>
                    <ul class="breadcrumb__content--menu d-flex justify-content-center">
                        <li class="breadcrumb__content--menu__items"><a class="text-white" href="index.html">Home</a></li>
                        <li class="breadcrumb__content--menu__items"><span class="text-white">My Account</span></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</section>
</main>
      {/* <!-- Start checkout page area --> */}
      <div class="checkout__page--area">
        <div class="container">
          <div class="checkout__page--inner d-flex">
            <div class="main checkout__mian">
              <header class="main__header checkout__mian--header mb-30">
                <details class="order__summary--mobile__version">
                  <summary class="order__summary--toggle border-radius-5">
                    <span class="order__summary--toggle__inner">
                      <span class="order__summary--toggle__icon">
                        <svg width="20" height="19" xmlns="http://www.w3.org/2000/svg">
                          <path d="M17.178 13.088H5.453c-.454 0-.91-.364-.91-.818L3.727 1.818H0V0h4.544c.455 0 .91.364.91.818l.09 1.272h13.45c.274 0 .547.09.73.364.18.182.27.454.18.727l-1.817 9.18c-.09.455-.455.728-.91.728zM6.27 11.27h10.09l1.454-7.362H5.634l.637 7.362zm.092 7.715c1.004 0 1.818-.813 1.818-1.817s-.814-1.818-1.818-1.818-1.818.814-1.818 1.818.814 1.817 1.818 1.817zm9.18 0c1.004 0 1.817-.813 1.817-1.817s-.814-1.818-1.818-1.818-1.818.814-1.818 1.818.814 1.817 1.818 1.817z" fill="currentColor"></path>
                        </svg>
                      </span>
                      <span class="order__summary--toggle__text show">
                        <span>Show order summary</span>
                        <svg width="11" height="6" xmlns="http://www.w3.org/2000/svg" class="order-summary-toggle__dropdown" fill="currentColor"><path d="M.504 1.813l4.358 3.845.496.438.496-.438 4.642-4.096L9.504.438 4.862 4.534h.992L1.496.69.504 1.812z"></path></svg>
                      </span>
                      <span class="order__summary--final__price">$227.70</span>
                    </span>
                  </summary>
                  <div class="order__summary--section">
                    <div class="cart__table checkout__product--table">
                      <table class="summary__table">
                        <tbody class="summary__table--body">
                          {orderItems &&
                            orderItems.map((item) => (
                              <>
                                <tr class=" summary__table--items" key={item.product}>
                                  <td class=" summary__table--list">
                                    <div class="product__image two  d-flex align-items-center">
                                      <div class="product__thumbnail border-radius-5">
                                        <a href="product-details.html"><img class="border-radius-5" src={item.image} alt="cart-product" /></a>
                                        <span class="product__thumbnail--quantity">{item.quantity}</span>
                                      </div>
                                      <div class="product__description">
                                        <h3 class="product__description--name h4">
                                          <Link to={`/Product-detail/${item.product}`}>
                                            {item.name}</Link></h3>
                                      </div>
                                    </div>
                                  </td>
                                  <td class=" summary__table--list">
                                    <span class="cart__price">{item.quantity} X Rs {item.price} ={" "}
                                      <b>RS {item.price * item.quantity}</b></span>
                                  </td>
                                </tr>
                              </>
                            ))}
                        </tbody>
                      </table>
                    </div>
                    <div class="checkout__discount--code">
                      <form class="d-flex" action="#">
                        <label>
                          <input class="checkout__discount--code__input--field border-radius-5" placeholder="Gift card or discount code" type="text" />
                        </label>
                        <button class="checkout__discount--code__btn primary__btn border-radius-5" type="submit">Apply</button>
                      </form>
                    </div>
                    <div class="checkout__total">
                      <table class="checkout__total--table">
                        <tbody class="checkout__total--body">
                          <tr class="checkout__total--items">
                            <td class="checkout__total--title text-left">Subtotal </td>
                            <td class="checkout__total--amount text-right">$860.00</td>
                          </tr>
                          <tr class="checkout__total--items">
                            <td class="checkout__total--title text-left">Shipping</td>
                            <td class="checkout__total--calculated__text text-right">Calculated at next step</td>
                          </tr>
                        </tbody>
                        <tfoot class="checkout__total--footer">
                          <tr class="checkout__total--footer__items">
                            <td class="checkout__total--footer__title checkout__total--footer__list text-left">Total </td>
                            <td class="checkout__total--footer__amount checkout__total--footer__list text-right">$860.00</td>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                  </div>
                </details>
              </header>
              <main class="main__content_wrapper">
                <form action="#">
                  <div class="checkout__content--step section__shipping--address pt-0">
                   
                    <div class="order__confirmed--area border-radius-5 mb-15">
                      <h3 class="order__confirmed--title h4">Your order is </h3>
                      <div>
                        <p
                          className={
                            paymentInfo &&
                              paymentInfo.status === "succeeded"
                              ? "greenColor"
                              : "redColor"
                          }
                        >
                          {paymentInfo &&
                            paymentInfo.status === "succeeded"
                            ? "PAID"
                            : "NOT PAID"}
                        </p>
                      </div>
                    </div>
                    <div class="customer__information--area border-radius-5">
                      <h3 class="customer__information--title h4">Customer Information</h3>
                      <div class="customer__information--inner d-flex">
                        <div class="customer__information--list">
                          <div class="customer__information--step">
                            <h4 class="customer__information--subtitle h5">Customer Name</h4>
                            <ul>
                              <li><a class="customer__information--text__link">{user && user.name}</a></li>
                            </ul>
                          </div>
                          <div class="customer__information--step">
                            <h4 class="customer__information--subtitle h5">Shipping address</h4>
                            <ul>
                              <li><span class="customer__information--text">{shippingInfo && shippingInfo.address}</span></li>
                              <li><span class="customer__information--text">{shippingInfo && shippingInfo.phoneNo}</span></li>
                              <li><span class="customer__information--text">{shippingInfo && shippingInfo.city}</span></li>
                              <li><span class="customer__information--text">{shippingInfo && shippingInfo.country},{shippingInfo && shippingInfo.state}</span></li>
                            </ul>
                          </div>

                        </div>
                        <div class="customer__information--list">
                          <div class="customer__information--step">
                            <h4 class="customer__information--subtitle h5">Email</h4>
                            <ul>
                              <li><span class="customer__information--text">{user&&user.email}</span></li>
                            </ul>
                          </div>

                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="checkout__content--step__footer d-flex align-items-center">
                    <a class="previous__link--content" ><Link to="/account">Return to Order</Link></a>
                  </div>
                </form>
              </main>
              <footer class="main__footer checkout__footer">
                <p class="copyright__content">Copyright © 2023 <a class="copyright__content--link text__primary" href="index.html">UsmanB</a> . All Rights Reserved.Design By UsmanB</p>
              </footer>
            </div>
            <aside class="checkout__sidebar sidebar">
              <div class="cart__table checkout__product--table">
                <table class="cart__table--inner">
                  <tbody class="cart__table--body">
                    {orderItems &&
                      orderItems.map((item) => (
                        <>


                          <tr class="cart__table--body__items" key={item.product}>
                            <td class="cart__table--body__list">
                              <div class="product__image two  d-flex align-items-center">
                                <div class="product__thumbnail border-radius-5">
                                  <a href="product-details.html"><img class="border-radius-5" src={item.image} alt="cart-product" /></a>
                                  <span class="product__thumbnail--quantity">{item.quantity}</span>
                                </div>
                                <div class="product__description">
                                  <h3 class="product__description--name h4">
                                    <Link to={`/Product-detail/${item.product}`}>
                                      {item.name}</Link></h3>
                                  <span class="product__description--variant">COLOR: Blue</span>
                                </div>
                              </div>
                            </td>
                            <td class="cart__table--body__list">
                              <span class="cart__price"> {item.quantity} X Rs {item.price} ={" "}
                                <b>RS {item.price * item.quantity}</b></span>
                            </td>
                          </tr>
                        </>
                      ))}

                  </tbody>
                </table>
              </div>
              <div class="checkout__discount--code">
                <form class="d-flex" action="#">
                  <label>
                    <input class="checkout__discount--code__input--field border-radius-5" placeholder="Gift card or discount code" type="text" />
                  </label>
                  <button class="checkout__discount--code__btn primary__btn border-radius-5" type="submit">Apply</button>
                </form>
              </div>
              <div class="checkout__total">
                <table class="checkout__total--table">
                  <tbody class="checkout__total--body">
                    <tr class="checkout__total--items">
                      <td class="checkout__total--title text-left">Subtotal </td>
                      <td class="checkout__total--amount text-right">RS {order && order.itemsPrice}</td>
                    </tr>
                    <tr class="checkout__total--items">
                      <td class="checkout__total--title text-left">Shipping</td>
                      <td class="checkout__total--calculated__text text-right">{order && order.shippingPrice}</td>
                    </tr>
                    <tr class="checkout__total--items">
                      <td class="checkout__total--title text-left">GST :</td>
                      <td class="checkout__total--calculated__text text-right">RS {order && order.taxPrice}</td>
                    </tr>
                  </tbody>
                  <tfoot class="checkout__total--footer">
                    <tr class="checkout__total--footer__items">
                      <td class="checkout__total--footer__title checkout__total--footer__list text-left">Total </td>
                      <td class="checkout__total--footer__amount checkout__total--footer__list text-right">RS {order && order.totalPrice}</td>
                    </tr>
                  </tfoot>
                </table>
              </div>
            </aside>
          </div>
        </div>
      </div>
      {/* <!-- End checkout page area --> */}
    </>
  );
};

export default OrderDetails;
